import { environment } from 'src/environments/environment';
import { GetComputer } from './../models/computer.model';
import { BehaviorSubject, Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {take, tap } from 'rxjs/operators';

const apiUrl = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})

export class ComputersService {

  private _computers = new BehaviorSubject<GetComputer[]>([]);
  public computers = this._computers.asObservable();

  constructor(private request: HttpClient) { }

  _getcomputers(){
    return this.getComputers();
  }

  private getComputers(){
    return this.request
    .get<GetComputer[]>(apiUrl + "/Product")
    .pipe(
      take(1)
    );
  }

}